const debug = false
const timer = false

export default function Filter (allLogs, filter) {
  // if (debug) console.log('>>>> Filter', allLogs, filter)
  if (debug) console.log(`::: Filter channel '${filter.get('channel')}', name '${filter.get('name')}', msg '${filter.get('msg')}'`)
  // console.log(filter.find(value => value !== ''))

  if (filter.find(value => value !== '') === undefined) {
    if (debug) console.log('Filter return { logs: allLogs, length: allLogs.size }')
    return { logs: allLogs, length: allLogs.size, scrollBottom: true }
  }
  if (timer) console.time('Filter')

  const CHANNEL = filter.get('channel')
  const NAME = filter.get('name')
  const MSG = filter.get('msg').toLowerCase()

  if (debug) console.log(`::: Filter channel '${CHANNEL}', name '${NAME}', msg '${MSG}'`)

  const logs = allLogs.filter(item => {
    // if (LEVEL && item.level !== LEVEL) { // genaue übereinstimmung
    //   return false
    // }

    // if (LEVEL && item.level >= LEVEL) { // ab diesem Level
    //   return false
    // }

    if (CHANNEL && item.chan !== CHANNEL) {
      return false
    }

    if (NAME && item.name !== NAME) {
      return false
    }

    if (MSG && item.msg.toLowerCase().indexOf(MSG) === -1) {
      if (item.name && item.name.toLowerCase().indexOf(MSG) === -1) {
        return false
      } if (!item.name) {
        return false // Alle Status Meldungen müssen auch weg
      }
    }

    // if (MSG && item.msg.toLowerCase().indexOf(MSG) === -1) {
    //   return false
    // }

    // if (MSG && item.name && item.name.indexOf(MSG) === -1) { // Überprüfe in der SearchBar die Nachricht und den Nickname
    //   return false
    // }

    return true // true ist das Ergebnis
  })
  if (timer) console.timeEnd('Filter')
  return { logs: logs, length: logs.size }
}

